  width: 185px;
  height: 96px;
  transform: translateX(-50%);
  z-index: 1;
}
.main-header__logo--inner:hover {
  cursor: pointer;
  filter: drop-shadow(0 -2px 5px #333333);
}
.main-header__logo--inner:active {
  filter: drop-shadow(0 -2px 3px #333333);
}
.main-header__logo--inner:focus-visible {
  outline: none;
  filter: drop-shadow(0 -2px 5px #333333);
}
@media (min-width: 768px) {
  .main-header__logo {
    z-index: 10;
  }
}

.main-header__logo-image {
  display: block;
}

.main-nav {
  position: relative;
  width: 100%;
  min-height: 55px;
}

.main-nav__toggle {
  background-color: transparent;
  width: 70px;
  height: 51px;
  padding: 0;
  position: absolute;
  cursor: pointer;
  top: 0;
  right: 0;
  z-index: 10;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
}

.main-nav--closed .main-nav__toggle:hover::before {
  opacity: 0.6;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3019607843);
  box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.3019607843), 0 18px 0 0 rgba(129, 179, 210, 0.3019607843);
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 28px;
  right: 20px;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.3;
}

.main-nav__list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  width: 100%;
  text-align: center;
  background-color: #81b3d2;
  position: absolute;
  z-index: 5;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    background: #ffffff;
    font-size: 14px;
  }
}

.main-nav__list::after {
  content: "";
  position: absolute;
  display: block;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 10px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__list::after {
    display: none;
  }
}

.main-nav__item {
  box-shadow: 0 -1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .main-nav__item {
    box-shadow: none;
  }
  .main-nav__item:nth-child(4n+3) {
    grid-column-start: 4;
  }
}

.main-nav__link {
  text-decoration: none;
  color: inherit;
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-transform: uppercase;
  padding: 17px 55px 12px;
  text-align: center;
}
@media (max-width: 767px) {
  .main-nav__link:hover {
    background-color: #669ec0;
  }
  .main-nav__link:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3019607843);
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    display: inline-block;
    border: none;
    padding: 18px 20px 17px;
    color: #000000;
    font-size: 14px;
    line-height: 21px;
  }
  .main-nav__link:not(.main-nav__link--current):hover {
    background-color: transparent;
    color: #81b3d2;
  }
  .main-nav__link:not(.main-nav__link--current):active {
    background-color: transparent;
    color: #000000;
    opacity: 0.3;
  }
  .main-nav__link--current {
    color: #766357;
    cursor: default;
  }
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
}
